<template>
	<div class="statistical">
		<div class="main">
			<div class="header">
				<div class="h-title">
					品牌总数<span>{{ totalBrandCount }}</span
					>个
				</div>
				<div class="h-title">
					公用品牌<span>{{ publicBrandCount }}</span
					>个
				</div>
			</div>
			<div class="area-map" v-loading="loading" element-loading-spinner="iconfont icon-loading1" element-loading-background="rgba(0, 0, 0, 0)">
				<img src="~@/assets/images/farm-product-brand/shanghai_map.png" alt="" />
				<div class="statistical-item" :class="'statistical-item-' + (index + 1)" v-for="(item, index) in data" :key="item.areaId">
					<div class="dot"></div>
					<div class="line"></div>
					<div class="statistical-item-box" @click="toBrandList(item.areaId)">
						<div class="statistical-item-head">
							<span class="statistical-item-title">{{ item.name }}</span>
							<i class="iconfont icon-shang1-copy"></i>
						</div>
						<div class="statistical-item-body">
							<div class="statistical-item-desc">
								<span class="label">品牌总数</span>
								<span class="count">{{ item.count }}</span>
							</div>
							<div class="statistical-item-desc">
								<span class="label">公用品牌</span>
								<span class="count">{{ item.sharedCount }}</span>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import {areaLevelCorAreaId} from '@/common/fn';
export default {
	props: {
		farmAreaInfo: {
			type: Object,
			default: () => areaLevelCorAreaId()
		},
		data: {
			type: Array,
			default: () => []
		},
		loading: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			totalBrandCount: 0,
			publicBrandCount: 0
		};
	},
	created() {},
	computed: {},
	mounted() {
		// 获取统计信息
		this.getBrandStatics();
	},
	methods: {
		getBrandStatics() {
			this.$ajax
				.post(this.$api.brand.getPpBrandStatics, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType
				})
				.then(res => {
					if (res.data.respCode === 0) {
						this.totalBrandCount = res.data.obj.totalCount;
						this.publicBrandCount = res.data.obj.totalSharedCount;
					}
				});
		},
		toBrandList(id) {
			this.$router.push({
				name: 'farmProductBrandList',
				query: {
					areaId: id
				}
			});
		}
	},
	components: {}
};
</script>
